@import url(https://fonts.googleapis.com/css?family=Montserrat+Subrayada:regular,700);
*{box-sizing:border-box;margin:0;padding:0}img{display:block}html{font-family:'Montserrat', sans-serif}.App h1{font-weight:700;width:-webkit-max-content;width:max-content;margin:3rem auto 0}ul{list-style-type:none}

.Carousel{position:relative;width:390px;margin:0 auto}.Carousel__list{margin-top:5rem;overflow-x:hidden}.Prev,.Next{position:absolute;top:50%;padding:10px 12px;border:1px solid #e9e9e9;border-radius:20px;outline:none;cursor:pointer;background:#330f96;color:#fff;font-weight:bold}.Prev:active,.Next:active{box-shadow:0 0 5px 0 rgba(0,0,0,0.3)}.Prev{-webkit-transform:translate(-120%, -50%);transform:translate(-120%, -50%)}.Next{-webkit-transform:translate(120%, -50%);transform:translate(120%, -50%);right:0;margin-left:20px}.Container{display:flex;transition:-webkit-transform 0.3s cubic-bezier(0.83, 0.24, 0.48, 0.83);transition:transform 0.3s cubic-bezier(0.83, 0.24, 0.48, 0.83);transition:transform 0.3s cubic-bezier(0.83, 0.24, 0.48, 0.83), -webkit-transform 0.3s cubic-bezier(0.83, 0.24, 0.48, 0.83)}

